import axios from 'axios';

const url = 'api/assignments';
//Listen fam, im just gonna disable this warning for now because fixing it woudl require changing other ocde
/* eslint-disable no-async-promise-executor */
class AssignmentService {
    //Get Assignments
    static getAssignments() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url);
                const data = res.data;
                resolve(
                    data.map(assignment => ({
                        ...assignment,
                        createdAt: new Date(assignment.createdAt)
                    }))
                )
            } catch(err) {
                reject(err);
            }
        });
    }
    //Create Assignments
    static insertAssignment(assignment) {
        const reto = {
            assignment_name:assignment.assignment_name,
            file: assignment.file
        };
        console.log("reto",reto);
        return axios.post(url, reto);
    }
    //Delete Assignments
    static deleteAssignment(id) {
        return axios.delete(`${url}/${id}`);
    }
    //Delete All Assignments
    static async deleteAll() {
        console.log("ATTEMTPING TO DELETE ALL ASSIGNMENTS");
        let ids = [];
        let assignments = [];

        assignments = await AssignmentService.getAssignments();
        console.log("assignments",assignments);
        let len = assignments.length;
        for(let i = 0; i < len; i++) {
            ids.push(assignments[i]._id);
        }
        console.log("ids", ids);
        let len2 = ids.length;
        for(let i = 0; i < len2; i++) {
            await AssignmentService.deleteAssignment(ids[i]);
        }
       
    }
       
}


export default AssignmentService