<script>
import AssignmentService from '../AssignmentService'

	export default {
		data() {
			return {
				assignments: [],
                error: '',
                assignment_name:'',
                file:'',
			}
		},
        methods: {
            async onChange(e) {
                let file = e.target.files[0];
                let formData = new FormData();
                formData.append("pdf", file);

                this.file = formData
            },
            async uploadAssignment() {
                const assignment = {
                    assignment_name: this.assignment_name,
                    file: this.file
                }
                if (assignment.file != ''){
                await AssignmentService.insertAssignment(assignment)
                }
            },

            async deleteAll() {
               await AssignmentService.deleteAll()
            },
        },
        async created () {
            try {
                this.assignments = await AssignmentService.getAssignments()
            } catch (error) {
                this.error = error.message
            }
        },
	}
</script>
<template>
    <h1>Upload Assignment</h1>
     <input class="assignment_name" v-model="assignment_name" placeholder="Enter the Assignment's Name...">
     <br>
     <form enctype="multipart/form-data">
        <div class="dropbox">
          <input type="file" multiple  @change="onChange"
            accept="application/pdf" class="input-file">
            <p >
              Drag your file(s) here to begin<br> or click to browse
            </p>
           
        </div>
      </form>
        <br>
        <button class="btn btn-primary" @click="uploadAssignment">Upload Assignment</button>
        <button class="btn btn-primary" @click="deleteAll">Danger Button</button>

</template>

<style scoped>
.dropbox {
    outline: 2px dashed grey; /* the dash box */
    outline-offset: -10px;
    background: lightcyan;
    color: dimgray;
    padding: 10px 10px;
    min-height: 200px; /* minimum height */
    position: relative;
    cursor: pointer;
  }
  
  .input-file {
    opacity: 0; /* invisible but it's there! */
    width: 100%;
    height: 200px;
    position: absolute;
    cursor: pointer;
  }
  
  .dropbox:hover {
    background: lightblue; /* when mouse over to the drop zone, change color */
  }
  
  .dropbox p {
    font-size: 1.2em;
    text-align: center;
    padding: 50px 0;
  }
</style>