<script>
import AssignmentService from '../AssignmentService'
	export default {
		data() {
			return {
				assignments: [],
                 error: '',
                text:'',
                file:'',
				search: ''
			}
		},
        async created () {
            try {
                this.assignments = await AssignmentService.getAssignments()
            } catch (error) {
                this.error = error.message
            }
        },
	}
</script>

<template>
     <input class="search-bar" v-model="search" placeholder="Search for an assignment...">
     <p>Search is {{search}} </p>
     <h1>Reccomended Assignments</h1>

     <p class="error" v-if="error">{{ error }}</p>
     <div class="assignments-container">

         <div class="assignments-list" v-for="(assignment, index) in assignments" :key="assignment._id" :item="assignment" :index="index">
                 <p class="text">{{assignment.assignment_name}}</p>
         </div>

     </div>
</template>

